import React from "react";

const ServiceContent = [
  {
    icon: "icon-mobile",
    title: "Human Factors",
    descriptions: `Interdisciplinary work emphasizing the interaction of human capacity, ergonomics and performance psychology
    within the performance environment.`,
    delayAnimation: "",
  },
  {
    icon: "icon-desktop",
    title: "MLOps",
    descriptions: `Emphasis in natural language processing, random forest classifiers and performance forecasting
    to monitor and predict changes in the perfomrance environment. `,
    delayAnimation: "200",
  },
  {
    icon: "icon-target",
    title: "Leadership & Innovation",
    descriptions: `Leverage data, machine learning, and performance psychology 
    to monitor and optimize human capabilities within complex socio-technical environments.`,
    delayAnimation: "400",
  },
];
export default function Service() {
  return (
    <>
      <div className="row">
        {ServiceContent.map((val, i) => (
          <div
            className="col-md-6 col-lg-4 my-3"
            key={i}
            data-aos="fade-right"
            data-aos-duration="1200"
            data-aos-delay={val.delayAnimation}
          >
            <div className="feature-box-01">
              <div className="icon">
                <i className={`icon ${val.icon}`}></i>
              </div>
              <div className="feature-content">
                <h5>{val.title}</h5>
                <p>{val.descriptions}</p>
              </div>
            </div>
            {/* End .feature-box-01 */}
          </div>
        ))}
      </div>
    </>
  );
}
